iar\" o \"nutrir\", reflejando su función esencial. Luego, se explica que la educación ayuda al ser humano a adaptarse a su entorno social, cultural y familiar, favoreciendo su desarrollo y supervivencia. Finalmente, se resalta que la educación busca guiar y potenciar las capacidades internas del individuo, promoviendo su crecimiento integral. "}})])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"cajon color-acento-botones px-5 py-4"},[_c('p',{staticClass:"mb-0"},[_vm._v("La educación es un proceso social mediado por prácticas comunicativas que facilitan la construcción de significados compartidos y el desarrollo de competencias necesarias para una sociedad, actuando como una herramienta clave para mantener o transformar las estructuras sociales (Gaitán, 2008; ICFES, 1995).")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row justify-content-center mb-5"},[_c('div',{staticClass:"col-sm-12 col-lg-5 d-none d-lg-block"},[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img9.png")}})]),_c('div',{staticClass:"col-sm-12 col-lg-7"},[_c('div',{staticClass:"bgimg1 mb-3"},[_c('h5',[_vm._v("Reflexión")]),_c('p',{staticClass:"mb-0"},[_vm._v("Piense en cómo definiría la educación basándose en su propia experiencia. ¿Coincide su perspectiva con las ideas mencionadas? Reflexionar sobre otras definiciones de educación podría ayudar a repensar su enfoque educativo.")])]),_c('div',{staticClass:"br1"},[_c('h5',[_vm._v("Dato interesante:")]),_c('p',{staticClass:"mb-0"},[_vm._v("¿Sabía que Paulo Freire (1921-1999), destacado educador brasileño, es reconocido como uno de los mayores exponentes de la pedagogía crítica? Su enfoque pedagógico planteaba un proyecto social alternativo y utópico.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _